import React from 'react';
import { MDBContainer, MDBRow, MDBCol, MDBJumbotron, MDBCardImage, MDBCardBody, MDBCardTitle, MDBCardText } from 'mdbreact';
import members from '../../members.js';
import './style.css';

class Detail extends React.Component {

    constructor (props) {
        super(props)
        
        this.state = {
            name: this.props.name
        }
        console.log(props);
        console.log(members[this.state.name]);
    }

    render() {
        return(
            <MDBRow className="member-detail">
                {/* <MDBCol md="12">
                    <MDBCardTitle className="h1 m-4 name text-center">
                            {members[this.state.name].name}
                        </MDBCardTitle>
                        <p class="title text-center">{members[this.state.name].title}</p>
                </MDBCol> */}
                <MDBCol>
                    <MDBCol md="12" className="member-img">
                        <img 
                        src={members[this.state.name].img} 
                        className="img-fluid"
                        />
                        {/* <MDBCardTitle className="h1 m-4 name">
                            {members[this.state.name].name}
                        </MDBCardTitle>
                        <p class="title">{members[this.state.name].title}</p> */}
                    </MDBCol>
                </MDBCol>
                <MDBCol>
                    <MDBCardBody>
                        <MDBCardTitle className="h1 m-4 name">
                            {members[this.state.name].name}
                        </MDBCardTitle>
                        <p class="title">{members[this.state.name].title}</p>
                        <MDBCardText className="bio">
                            {members[this.state.name].bio}
                            <br />
                        </MDBCardText>
                        <MDBCardText className="bio">
                            {members[this.state.name].edu}
                        </MDBCardText>
                    </MDBCardBody>
                </MDBCol>
            </MDBRow>
        )
    }
}

export default Detail;