import React from 'react';
import { MDBCard, MDBCardBody, MDBCardImage, MDBCardTitle, MDBCardText, MDBCol } from 'mdbreact';
import members from '../../members.js';
import './style.css';

class List extends React.Component {

    printCard = key => 
        <MDBCol  md="4">
            <a href={'/catering/team/' + key} className="col-md-12">
                <MDBCard className="list-card">
                    {
                        members[key].img === null ?
                        <div class="view card-img" style={{backgroundColor: '#e1e1e1'}}></div>
                        :
                        <div class="view card-img" style={{backgroundImage: `url(${members[key].img})`}}></div>
                    }
                    {/* <MDBCardImage className="img" src={members[key].img}/> */}
                    <MDBCardBody>
                        <MDBCardTitle>{members[key].name}</MDBCardTitle>
                        <MDBCardText>
                            {members[key].title}
                        </MDBCardText>
                    </MDBCardBody>
                </MDBCard>
            </a>  
        </MDBCol>
    
    render() {
        const names = [
			'raymie_grimm',
            'charlene_james',
            'johnique_woods',
            'lisa_naik',
            'alex_ortega',
            'angelique_leon',
            'amanda_murphy',
            'judy_stout',
            'beaney_cota',
            'stephanie_bixby',
            'michael_omo',
            'lawrence_sanchez',
            'manja_blackwood',
        ];

        return (
            <React.Fragment>
            {
                names.map(key => {
                    return this.printCard(key);
                })
            }
            </React.Fragment>
        )
    }
}
        
export default List;